* {
  box-sizing: border-box;
  font-family: 'Courier New', Courier, monospace;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}
/* This was an edit that Louis helped me with, enabled the images to be displayed in rows instead of columns. */
.container {
  display: flex;
  flex-wrap: wrap;
}
.card {
  border: 4px rgb(99, 99, 99) solid;
  border-radius: 2px;
  width: 150px;
  height: 150px;
  margin: 1rem;
  position: relative;
  box-shadow: 4px 4px 10px rgb(56, 56, 56);
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  background:rgb(255, 255, 255);
}

.card > .img-container > img {
  height: 100%;
}

.card:hover {
  box-shadow: 8px 8px 20px rgb(56, 56, 56);
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  cursor: pointer;
}

.card:active {
  -webkit-transform: translate(1px, 1px);
          transform: translate(1px, 1px);
}
nav {
    width: 100%;
    font-size: 3rem;
    color: #fff;
    margin-bottom: 50px;
    background-color: #388147;
    height: auto;
    font-family: 'Bungee', cursive;
  }
  
  a {
    color: #fbb844;
  }
  
  a:hover {
    color: #fff;
    text-decoration: none;
  }
  
  nav li {
    display: inline-block;
    padding: 26px 0;
    width: 25%;
    /* text-align: center; */
  }
  
  ul {
    /* padding-left: 0px; */
    display: flex;
    align-items: center;
  }
  
  @media only screen and (max-width: 750px) {
    #cur-sco, #top-sco {
      font-size: 2rem;
    }
  }
